<template>
   <md-content>
       	
				<md-field>
					<label>Usuarios actuales</label>
					<md-input v-model="users.act" type="number"></md-input>
				</md-field>

				<md-field>
					<label>Usuarios nuevos</label>
					<md-input v-model="users.new" type="number"></md-input>
				</md-field>

				<md-field>
					<label>Usuarios futuros</label>
					<md-input v-model="users.fut" type="number"></md-input>
				</md-field>

				<md-button class="md-raised md-primary" @click="save('step_1',[{'usrfutr':users[2],'usract':users[0],'usrnew':users[1]}])">Guardar</md-button>
			
    </md-content>
</template> 

<script type="text/javascript">
export default {
  name: "Step1",
  props: {
    disabled: {
      type: Boolean,
      required: false
    }
  },
  data() {
    return {
      users: {
        act: null,
        new: null,
        fut: null
      }
    };
  },
  methods: {
    save: function(table, list) {
      eel.save(table, list)();
    }
  }
};
</script> 



